import { NextResponse } from "next/server";
import os from "os";

export async function GET() {
  try {
    const interfaces = os.networkInterfaces();
    let networkAddress = "localhost:3000";

    // Find the first non-internal IPv4 address
    for (const name of Object.keys(interfaces)) {
      const iface = interfaces[name];
      if (!iface) continue;

      for (const alias of iface) {
        if (alias.family === "IPv4" && !alias.internal) {
          networkAddress = `${alias.address}:3000`;
          break;
        }
      }
      if (networkAddress !== "localhost:3000") break;
    }

    const url = `http://${networkAddress}`;

    return NextResponse.json({ url, address: networkAddress });
  } catch (error) {
    console.error("Error getting network address:", error);
    return NextResponse.json(
      { url: "http://localhost:3000", address: "localhost:3000" },
      { status: 500 }
    );
  }
}
